.App-game {
  text-align: center;
}

.App-header {
  background-color: #271d14;
}

.App-header .background-header {
  max-width: 100%;
    margin: 0 auto;
    display: block;
}

.App-header .titulos{
  color: #fff;
  text-align: center;
  padding: 10px;
}

.App-header .App-title, .App-header h2{
  margin: 0;
}

.App-header .App-title{
  text-transform: uppercase;
  font-size: 34px;
}

.App-header h2{
  font-size: 24px;
}

.header-game{
  border: 5px solid #a93322;
  border-radius: 5px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
} 

.header-game p{
  font-size: 20px;
    font-style: italic;
    margin: 15px;
}

section.game{
  padding: 20px;
}

.game-stage p{
  font-size: 15px;
  text-align: justify;
}

.game-stage p strong { 
  font-size: 50px; 
  color: #a93322; 
    font-family: initial;
    vertical-align: bottom;
}

.game-stage .btn-game {
    background-color: #a93322;
    border: none;
    border-radius: 5px;
    color: #fff;
    display: inline-block;
    font-weight: bold;
    margin: 10px;
    padding: 10px 20px;
    text-decoration: none;
    text-transform: uppercase;
}

#jogo0{
  display: block;
}

#jogo0 .App-intro{
  font-weight: bold;
}

footer {
  background-color: #271d14;
  text-align: center;
  padding: 10px;
  font-family: sans-serif;
}

footer p {
  color: #fff;
  margin: 0;
  text-transform: uppercase;
}

footer a {
  color: #fff;
  text-decoration: none;
}



